import {
  Component,
  OnInit,
  Input
} from '@angular/core';
import {
  SharedModuleEventHandlerService,
  sharedModuleEventHandlerType
} from '@shared-event-handler';

@Component({
  selector: 'tce-web-lesson-objectives',
  templateUrl: './lesson-objectives.component.html',
  styleUrls: ['./lesson-objectives.component.scss']
})
export class LessonObjectivesComponent implements OnInit {
  public display = true;
  @Input() public data: any;
  public isPassed: boolean;

  constructor(
    private sharedModuleEventHandlerService: SharedModuleEventHandlerService
  ) { }

  public ngOnInit(): void {
    console.log(this.data);

    this.sharedModuleEventHandlerService.on(
      sharedModuleEventHandlerType.LESSON_OBJECTIVE_TOGGLE,
      data => this.display = data
    );

    if (this.data) {
      if (this.data.timeTaken > this.data.timeGiven) {
        this.isPassed = false;
      } else {
        this.isPassed = true;
      }
    }
  }
}
